define('controllers/movieController',["modules/app","services/movieHttpFacade"],function(app,movieHttpFacade){
   app.controller("movieController",["$scope","movieHttpFacade",function($scope,movieHttpFacade){
       
       $scope.error = false;
        movieHttpFacade.getSampleMovie().success(function(data){
        $scope.movie = data;
        }).error(function(error){
            $scope.error = true;
        });
       
   }]);
    
});
