define('services/movieHttpFacade',["modules/app"],function(app){
    app.factory("movieHttpFacade",["$http",function($http){
        var _getSampleMovie = function(){
            return $http.get("http://www.omdbapi.com/?t=Titanic&y=&plot=full&r=json")
        };
        
        return{
            getSampleMovie: _getSampleMovie
        };
        
    }]);
    
});
